.loader-container {
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.loader {
  height: 50px;
  width: 50px;
  animation: rotate 1s infinite;
}

.loader:before, .loader:after {
  content: "";
  height: 20px;
  width: 20px;
  display: block;
}

.loader:before {
  background-color: #000;
  margin-bottom: 10px;
  animation: box1 1s infinite;
  box-shadow: 30px 0 #ff3d00;
}

.loader:after {
  background-color: #ff3d00;
  animation: box2 1s infinite;
  box-shadow: 30px 0 #000;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg)scale(.8);
  }

  50% {
    transform: rotate(360deg)scale(1.2);
  }

  100% {
    transform: rotate(720deg)scale(.8);
  }
}

@keyframes box1 {
  0% {
    box-shadow: 30px 0 #ff3d00;
  }

  50% {
    margin-bottom: 0;
    transform: translate(15px, 15px);
    box-shadow: 0 0 #ff3d00;
  }

  100% {
    margin-bottom: 10px;
    box-shadow: 30px 0 #ff3d00;
  }
}

@keyframes box2 {
  0% {
    box-shadow: 30px 0 #000;
  }

  50% {
    margin-top: -20px;
    transform: translate(15px, 15px);
    box-shadow: 0 0 #000;
  }

  100% {
    margin-top: 0;
    box-shadow: 30px 0 #000;
  }
}

#main .movie:hover {
  cursor: pointer;
}

.is-hidden {
  display: none;
}

